						<view class="code-value-wrapper">
							<text class="code-value">{{ showCodes[index] ? code : maskCode(code) }}</text>
							<image class="eye-icon" :src="showCodes[index] ? '/static/images/ÁùÅÁúº.png' : '/static/images/Èó≠Áúº.png'" mode="aspectFit" @click="toggleCode(index)"></image>
						</view>
					</view>

					<button class="copy-btn" @click="handleCopyCode(code)" hover-class="button-hover">Â§çÂà∂</button>
				</view>
				</view>
				<view class="redemption-tip">
					<text class="tip-icon">üí°</text>
					<text class="tip-text">Â§çÂà∂ÂÖëÊç¢Á†ÅÂêéÂèØÂâçÂæÄ"‰∏™‰∫∫‰∏≠ÂøÉ-Á§ºÂìÅÂÖëÊç¢"ËøõË°åÂÖëÊç¢</text>
				</view>
				<button class="goto-redeem-btn" @click="handleGotoRedeem" hover-class="button-hover">Á´ãÂç≥ÂÖëÊç¢</button>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
		<view class="bottom-bar" v-if="order.orderStatus === 0">
			<button class="action-btn cancel-btn" @click="handleCancel" hover-class="button-hover">ÂèñÊ∂àËÆ¢Âçï</button>
			<button class="action-btn pay-btn" @click="handlePay" hover-class="button-hover">Á´ãÂç≥ÊîØ‰ªò</button>
		</view>
		
		<!-- Â∑≤ÂÆåÊàêËÆ¢ÂçïÁöÑÊìç‰ΩúÊ†è -->
		<view class="bottom-bar" v-if="order.orderStatus === 1">
			<button class="action-btn reorder-btn" @click="handleReorder" hover-class="button-hover">
				<text class="btn-icon">üîÑ</text>
				<text>ÂÜçÊù•‰∏ÄÂçï</text>
			</button>
