<script>
import { getOrderDetail, cancelOrder as cancelOrderApi } from '../../api/order.js';

export default {
	data() {
		return {
			orderId: null,
			order: {},
			showCodes: {}
		}
	},
	onLoad(options) {
		if (options.id) {
			this.orderId = options.id;
			this.loadOrderDetail();
		}
	},
	methods: {
		// Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
		async loadOrderDetail() {
			try {
				uni.showLoading({
					title: 'Âä†ËΩΩ‰∏≠...',
					mask: true
				});
				
				const result = await getOrderDetail(this.orderId);
				this.order = result || {};
				
				uni.hideLoading();
			} catch (error) {
				uni.hideLoading();
				console.error('Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				});
			}
		},
		
		// Ëé∑ÂèñÁä∂ÊÄÅÂõæÊ†á
		getStatusIcon(status) {
			switch(status) {
				case 0:
					return '‚è∞';
				case 1:
					return '‚úì'; // ÁÆÄÁ∫¶ÁöÑÂØπÂãæ
				case 2:
					return '√ó'; // ÁÆÄÁ∫¶ÁöÑÂèâÂè∑
				default:
					return '‚óè';
			}
		},
		
		// Ê†ºÂºèÂåñÊó•ÊúüÊó∂Èó¥
		formatDateTime(dateTime) {
			if (!dateTime) return '-';
			const date = new Date(dateTime);
			const year = date.getFullYear();
			const month = String(date.getMonth() + 1).padStart(2, '0');
			const day = String(date.getDate()).padStart(2, '0');
			const hours = String(date.getHours()).padStart(2, '0');
			const minutes = String(date.getMinutes()).padStart(2, '0');
			return `${year}-${month}-${day} ${hours}:${minutes}`;
		},
		
		// Ê†ºÂºèÂåñËøáÊúüÊó∂Èó¥
		formatExpireTime(expireTime) {
			if (!expireTime) return '';
			const expire = new Date(expireTime);
			const now = new Date();
			const diff = expire - now;
			
			if (diff <= 0) return 'Â∑≤ËøáÊúü';
			
			const minutes = Math.floor(diff / 1000 / 60);
			if (minutes < 60) {
				return `${minutes}ÂàÜÈíü`;
			}
			
			const hours = Math.floor(minutes / 60);
			const remainMinutes = minutes % 60;
			return `${hours}Â∞èÊó∂${remainMinutes}ÂàÜÈíü`;
		},
		
		// ÂèñÊ∂àËÆ¢Âçï
		handleCancel() {
			uni.showModal({
				title: 'ÊèêÁ§∫',
				content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àÊ≠§ËÆ¢ÂçïÂêóÔºü',
				success: async (res) => {
					if (res.confirm) {
						try {
							uni.showLoading({ title: 'ÂèñÊ∂à‰∏≠...', mask: true });
							await cancelOrderApi(this.orderId);
							uni.hideLoading();
							uni.showToast({
								title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
								icon: 'success'
							});
							// Âà∑Êñ∞ËÆ¢ÂçïËØ¶ÊÉÖ
							setTimeout(() => {
								this.loadOrderDetail();
							}, 1500);
						} catch (error) {
							uni.hideLoading();
							console.error('ÂèñÊ∂àËÆ¢ÂçïÂ§±Ë¥•:', error);
						}
					}
				}
			});
		},
		
		// ÂâçÂæÄÊîØ‰ªò
		handlePay() {
			const firstItem = this.order.orderItems && this.order.orderItems.length > 0 ? this.order.orderItems[0] : {};
			
			uni.redirectTo({
				url: `/pages/payment/payment?orderId=${this.order.id}&orderNo=${encodeURIComponent(this.order.orderNo)}&productName=${encodeURIComponent(firstItem.productName || '')}&quantity=${firstItem.quantity || 1}&payAmount=${this.order.payAmount}`
			});
		},
		
		// Â§çÂà∂ÂÖëÊç¢Á†Å
		handleCopyCode(code) {
			uni.setClipboardData({
				data: code,
				success: () => {
					uni.showToast({
						title: 'ÂÖëÊç¢Á†ÅÂ∑≤Â§çÂà∂',
						icon: 'success'
					});
				},
				fail: () => {
					uni.showToast({
						title: 'Â§çÂà∂Â§±Ë¥•',
						icon: 'none'
					});
				}
			});
		},
		
		// Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫‰∏≠ÂøÉÔºàÂÖëÊç¢Âå∫ÂüüÔºâ
		handleGotoRedeem() {
			uni.switchTab({
				url: '/pages/personal/personal'
			});
		},
		
		// ÂÜçÊù•‰∏ÄÂçï
		handleReorder() {
			if (!this.order.orderItems || this.order.orderItems.length === 0) {
				uni.showToast({
					title: 'ËÆ¢ÂçïÂïÜÂìÅ‰ø°ÊÅØÁº∫Â§±',
					icon: 'none'
				});
				return;
			}
			
			// Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™ÂïÜÂìÅ
			const firstItem = this.order.orderItems[0];
			
			// Ë∑≥ËΩ¨Âà∞ÂïÜÂìÅËØ¶ÊÉÖÈ°µ
			uni.navigateTo({
				url: `/pages/product-detail/product-detail?id=${firstItem.productId}`
			});
		},
		
		// ÂàáÊç¢ÂÖëÊç¢Á†ÅÊòæÁ§∫/ÈöêËóè
		toggleCode(index) {
			this.showCodes[index] = !this.showCodes[index];
			// Âº∫Âà∂Êõ¥Êñ∞ËßÜÂõæ
			this.$forceUpdate();
		},
		
		// ÈÅÆÁΩ©ÂÖëÊç¢Á†Å
		maskCode(code) {
			if (!code || code.length <= 4) return '****';
			return code.substring(0, 2) + '****' + code.substring(code.length - 2);
		}
	}
}
</script>

